  max-height: 100vh;
  min-height: -webkit-fill-available;
  #main {
    transition: 1s ease opacity;
    opacity: 0;
    background: white;
  }
  &.load {
    overflow-y: inherit;
    #main {
      opacity: 1;
    }
  }
}
#loader {
  position: absolute;
  left: 670px;
  top: 100px;
  bottom: 50px;
  right: 50px;
  z-index: -1;
  opacity: 0;
  @include down(md) {
    left: 50px;
    bottom: 300px;
  }
  @include down(sm) {
    left: 50px;
    bottom: 200px;
  }
  .spin {
    position: absolute;
    width: 150px;
    height: 150px;
    background: url('img/icono_carga_02.png');
    background-size: contain;
    background-repeat: no-repeat;
    transition: all .3s;
    .before {
      transition: all .3s;
      display: block;
      margin-top: 46px;
      margin-left: 50px;
      width: 50px;
      height: 60px;
      background: url('img/icono_carga.png');
      background-size: contain;
      background-repeat: no-repeat;
      
    //   // animation-name: rotate;
    //   // animation-duration: 3000ms;
    //   // animation-iteration-count: infinite;
    //   // animation-timing-function: linear; 
    //   // transform-style: preserve-3d;
      animation-name: spin;
      animation-duration: 8000ms;
      animation-iteration-count: infinite;
      animation-timing-function: linear; 
    }
  }
}

@-webkit-keyframes rotate {
  from { -webkit-transform: rotateY(0deg);    }
  to   { -webkit-transform: rotateY(-360deg); }
}

@keyframes spin {
  from {
      transform:rotate(0deg);
  }
  to {
      transform:rotate(360deg);
  }
}

