      border-style: solid;
      border-width: 0px 8px 12px 8px;
      border-color: #f9f9f9 transparent #f9f9f9 transparent;
      position: absolute;
      top: -12px;
      left: 242.5px;
  }
  .imgs {
    font-size: 0;
    a {
      width: 33.33%;
      margin: 0;
      display: inline-block;
      img {
        margin: 1px;
        width: calc(100% - 2px);
        animation: opacitypulse .6s infinite ease-in-out;
      }
      &.active img {
        animation: none;
      }
    }
  }
  .follow {
    text-align: center;
    margin: 10px 0 0 0;
    a {
      margin: 0;
      font-size: 1rem;
    }
  }
}

@keyframes opacitypulse {
  0% { opacity: 0.7; }
  50% {  opacity: 1; }
  100% { opacity: 0.7; }
}

@keyframes topdownpulse {
  0% { transform: translateY(-10px); }
  50% {  transform: translateY(0px);  }
  100% { transform: translateY(-10px);  }
}

[js-go] {
  cursor: pointer;
}


.animation-intro {
  width: 100%;
  max-height: 100vh;
  min-height: -webkit-fill-available;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
  overflow: hidden;
  position: relative;
  * {
    transition: .6s all;
  }
  .center {
    width: 132px;
  }
  .container-waves {
    position: absolute;
    top: auto;
    bottom: -80px;
    left: 0;
    right: 0;
    // background: rgba(#b10832,.5);
    background: #b10832;
    // background: blue;
    animation: 8s move-to-top forwards cubic-bezier(.55,.5,.45,.5);
    height: 115vh;
    display: none;
    .waves {
      position:relative;
      width: 100%;
      top:-78px;
      min-height:80px;
      max-height:80px;
      * {
        fill: rgba(#b10832,1);
      }
    }

    .parallax > use {
      animation: move-forever .3s cubic-bezier(.55,.5,.45,.5) infinite;
    }
    .parallax > use:nth-child(1) {
      animation-delay: -2s;
      animation-duration: 2s;
    }
    .parallax > use:nth-child(2) {
      animation-delay: -3s;
      animation-duration: 10s;
    }
    .parallax > use:nth-child(3) {
      animation-delay: -4s;
      animation-duration: 13s;
    }
    .parallax > use:nth-child(4) {
      animation-delay: -5s;
      animation-duration: 20s;
    }
  }
  
  canvas {
    padding-top: 0;
    padding-bottom: 10px;
    z-index: 1000;
    position: relative;
    max-width: 100vw;
  }

  &.displayLateral {
    .center {
      width: 500px;
    }
  }

  .bottomrigth {
    transition: .6s all;
    top: auto;
    left: 0;
    bottom: 40px;
    right: auto;
    position: absolute;
    z-index: 1000;
    font-weight: 600;
    color: white;
    opacity: 0;
    font-size: 5rem;
    line-height: 6rem;
    @include down(md) {
      font-size: 4rem;
      line-height: 5rem;
    }
    @include down(sm) {
      font-size: 2rem;
      line-height: 2.8rem;
    }
  }

  .down {
    transition: .6s all;
    top: auto;
    left: auto;
    bottom: 40px;
    right: 0;
    position: absolute;
    z-index: 1004;
    color: white;
    opacity: 0;
    cursor: pointer;
    img {
      width: 50px;
      animation: topdownpulse 3s infinite ease-in-out;
    }
    @include down(sm) {
      right: 20px !important;
      img {
        width: 30px;
      }
    }
  }
}

