#logo-svg {
  overflow: hidden;
  // transition: .6s all;
  // @include down(sm) {
  //   width: 80%;
  //   margin: 0 auto;
  // }
  transition: .6s all;
  * {
    transition: .6s all;
  }
  @include down(sm) {
    transform: scale(.50);
    transform-origin: top center;
  }

  #logo2 {
    opacity: 0;
    transform: translate(-132px, -300px);
    
  }
  #cover-rect {
    transition: 1s all;
    fill: white !important;
    transform: translate(0, 116px);
  }
  &.doubleVila {
    #logo2 {
      transform: translate(-132px, -338px);
      opacity: 1;
    }
    #cover-rect {
      opacity: 0;

    }
  }
  &.logoRelleno {
    position: relative;
    z-index: 1;
    // &:after {
    //   content: '';
    //   transition: 1s all;
    //   background: $pink;
    //   width: 14px;
    //   height: 14px;
    //   top: 78px;
    //   left: 45px;
    //   position: absolute;
    //   display: block;
    //   z-index: 0;
    // }
  }
  svg {
    margin: 0 auto;
    overflow: visible;
  }
  g.vi {
    .recCover {
      fill:white !important;
      transform: translateX(-3px);
    }
  }
  g.lateral {
    opacity: 0;
    transform: translateX(-366px);
  }  
  &.koolRed {
 
    overflow: visible;
    * {
      fill: $pink;
    }
    .recCover {
      display: none;
    }
  }
  &.displayLateral {
    g.lateral {
      opacity: 1;
      transform: translateX(0);
    }
  }
}

.header-white #logo-svg * {
  fill: white !important;
}
.header-white #logo-svg.logoRelleno:before {
  // background: white !important;
