$font-family-sans-serif: 'CircularStd', sans-serif !default;

$h1-font-size: 2.8em;
$h2-font-size: 1.8rem;
$h3-font-size: 1rem;
$h4-font-size: 1rem;
$h5-font-size: 1rem;
$h6-font-size: 0.75rem;

$pink: #b5043a;

$green: #adc6a3;
$color-titles: #adc6a3;
$color-btns: #c6d3a8;


$font-size-base: 1rem;

$primary: $pink;

$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #eeeeee;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #404040;
$gray-700: #313131;
$gray-800: #343a40;
$gray-900: #2c2c2c;

$blue-1: #71abd3;
$blue-2: #3a84c2;
$blue-3: #283f4c;

$link-color: white;
$link-decoration: underline;

$border-radius: 0;
$border-radius-lg: 0;
$border-radius-sm: 0;

.hidden-md-up {
  @extend .d-md-none;
}


$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1480px
);

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 1280px,
  xl: 1400px
);
