.footer {
  // background: $blue-3;
  padding: 100px 0 50px 0;
  color: $pink;
  @include down(md) {
    padding: 50px 0 20px 0;
  }
  .d-flex {
    justify-content: space-between;
    align-items: flex-start;
    @include down(md) {
      flex-direction: column-reverse;
      flex-flow: row wrap;
    }
    @include down(sm) {
      flex-direction: column-reverse;
      // flex-flow: row wrap;
    }
  }
  .text {
    @include down(md) {
      width: inherit;
    }
  }
  .text2 {
    @include down(md) {
      width: 40%;
    }
    @include down(sm) {
      margin-bottom: 20px;
    }
  }
  .logo {   
    @include down(md) {
      padding-right: 0;
      border-right: 0;
      padding-bottom: 20px;
      margin-bottom: 20px;
    }
  }
  .form {
    width: 33.33%;
    @include down(md) {
      width: 100%;
      padding-bottom: 50px;
    }
    .t {
      margin-bottom: 10px;
      display: block;
    }
    .custom-control {
      margin-bottom: 10px;
      a {
        text-decoration: underline;
      }
    }
    .has-danger.custom-control .custom-control-label:before,
    .form-control-danger:focus,
    .form-control-danger {
      border-color: $pink !important;
      outline: $pink 2px solid;
    }
    // .has-danger.custom-control {
    //   color: black !important;
    //   a {
    //     color: black !important;
    //   }
    // }
  }
  .text {
    a {
      text-decoration: none;
      color: $pink;
      &:hover {
        color: black;
      }
    }
  }
  .social {
    margin-top: 30px;
    a {
      display: inline-block;
      background: white;
      color: $pink;
      text-align: center;
      line-height: 44px;
      vertical-align: middle;
      font-size: 44px;
      margin-right: 20px;
    }  
    @include down(md) {
      padding-right: 0;
      border-right: 0;
      padding-bottom: 20px;
      margin-bottom: 20px;
    }
  }
}
