.header-bg {
    background: white;
    transition: all .3s;
    height: 100px;
    left: 0;
    right: 0;
    top: 0;
    position: absolute;
    z-index: 1000;
}
.margin-header {
    margin-top: 100px;
}

.invisible-logo {
    position: relative;
    span {
        display: none;
    }
    svg {
        position: absolute;
        top: -15px;
        left: 8px;
        width: 170px;
        height: 60px;
        @include down(sm) {
            width: 130px;
        }
        #svg-logo2 {
            transform: translateY(-35px);
        }
    }
}

.header {
    z-index: 1011;
    position: absolute;
    padding: 35px 0;
    // position: absolute;
    transition: all .3s;
    top: 0;
    left: 0;
    right: 0;
    font-size: 26px;
    // background: white;
    font-weight: 300;
    text-align: center;
    // @include down(lg) {
    //     padding: 25px 0;
        
    // }
    svg {
        transition: all .3s;
    }
    .container {
        // justify-content: space-between;
    }
    .menu {
        margin: 0 -2%;
    }
    .invisible-logo {
        height: 40px;
        float: left;
        // background: red;
        width: 170px;
        @include down(sm) {
            width: 130px;
        }
    }
    .social {
        float: right;
        a {
            margin-right: 10px;
        }
        @include down(sm) {
            display:none;
        }
    }
    .rmenu-btn {
        padding-top: 15px;
        padding-bottom: 15px;
        margin-left: 20px;
        float: right;
    }
    a {
        color: $pink;
        text-decoration: none;
    }
    .logo {
        order: 0;
        float: left;
        position: relative;
        display: block;
        width: 300px;
        margin: 10px auto;
        img {
            width: 100%;
        }
        @include down(lg) {
            width: 250px;
            margin: 5px auto;
        }
        
        @include down(md) {
            margin-left: -15px;
        }
    }
    .select_lang {
        min-width: 90px;
        font-size: 16px;
        vertical-align: middle;
        .select2-container {
            min-width: 90px;
            .select2-selection__rendered {
                padding-left: 0;
            }
        }
        @include down(md) {
            margin-right: -15px;
        }
    }
    .select2-container--default .select2-selection--single {
        // padding: 10px 30px;
        border:none;
        outline: none !important;
    }
    .select2-selection__arrow {
        right: -5px !important;
     }
    .presentation {
        border-color: #333 transparent transparent transparent;
    }

}

#real-logo * {
    fill: $pink;
}

.header-scroll {
    .header {
        position: fixed;
        background-color: white;
        transition: all .3s;
        padding: 15px 0 10px 0;
        z-index: 1040;
        // &:hover {
        //     background-color: white;
    }
    #logo-svg svg,
    .invisible-logo svg {
        width: 130px !important;
        height: 80px;
    }
} 
.header-white.header-scroll:not(.open-rmenu) header.header {
    background-color: transparent;
}

.header-white:not(.open-rmenu) header.header {
    // z-index: 101;
    color: white;
    a {
        color:white;
    }
    .rmenu-btn .bars, .rmenu-btn .bars:before, .rmenu-btn .bars:after {
        background: white;
    }
    #real-logo * {
        fill: white;
    }
    
    
}
.header-white:not(.open-rmenu) .invisible-logo svg * {
    fill: white;
}
.header-white:not(.open-rmenu) .header-bg {
    opacity: 0;
}
