$rmenu-background: white;
$rmenu-color: $pink;
$rmenu-bordercolor: rgba(white, .1);
$rmenu-maxwidth: 400px;
$rmenu-zindex: 1000;
$transition: all ease .3s;

.rmenu {
	position: fixed;
	top: -100vh;
	left: 0;
	bottom: auto;
	right: 0;
	// width: 100vw;
	height: 100%;  
	// max-height: 100vh;
	// min-height: -webkit-fill-available;
	overflow: hidden;
	transition: $transition;
	background: $rmenu-background;
	color: $brand-primary;
	z-index: $rmenu-zindex;
	font-family: $font-family-sans-serif;
	.fa-times-thin:before {
		content: '\00d7';
	}
	.header-link-selected {
		color: $gray-dark;
	}
	&__ul {
		list-style: none;
		padding: 100px 0 0 0;
		height: 100%;
		position: relative;
		min-height: -webkit-fill-available;
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		@include down(md) {
			font-size: 60px;
			padding: 100px 0 0 0;
		}
		li {
			padding-left: 60px;
			line-height: 100%;
			position:relative;
			justify-content: flex-start;
			align-items: center;
			display: flex;
			background: $pink;
			cursor: pointer;
			&:last-child {
				padding: 0;
				@include up(sm) {
					display:none;
				}
			}
			color: $gray-dark;
			text-align: left;
			font-size: 60px;
			

			.close {
				display: none;
			}
			&:hover {
				background: $gray;
			}

			@include down(md) {
				font-size: 60px;
				text-align: center;
			}
			@include down(sm) {
				font-size: 40px;
			}
			.divider {
				font-weight: 700;
				padding-left: $spacer;
				color: $brand-danger;
			}
			a {
				color: white;
				position: relative;
				transition: color 0.5s ease;
				text-decoration: none!important;
				
				// &:hover {
				// 	color: $brand-danger;
				// }
				// &.header-link-selected {
				// 	font-weight: 700;
				// 	color: $brand-primary;
				// 	&:after {
				// 		max-width: 100%;
				// 	}
				// }
			}
			.main-nav__btn {
				float: left;
				right: 0;
			}
			.social-network-icons {
				float: right;
			}
			// &:first-child {
			// 	padding-bottom: $spacer*4;
			// }
			&:last-child {
				border-bottom: none;
			}
		}
	}
	.rmenu-close {
		top: 30px;
		@extend .close;
		&:before,
		&:after {
			background-color: #fff;
		}
		// &:hover {
		// 	color: $gray;
		// }
	}
	.social {
		@include up(md) {
			display:none;
		}
		display: flex;
		justify-content: space-between;
		width: 100%;
		padding: 0 50px;
		a {
			display: block;
			margin: 0 10px;
		}
		font-size: 40px;
	}
}
.rmenu-btn {
	transition: $transition;
	// @include up(lg) {
	// 	display: none;
	// }
	align-self: center;
	cursor: pointer;
	// position: absolute;
    // right: 25px;
    // top: 50px;
	.bars {
		transition: $transition;
		&,
		&:before,
		&:after {
			width: 35px;
			height: 4px;
			content: '';
			background: $rmenu-color;
			display: block;
			position: relative;
			transition: $transition;
		}
		&:before {
			top: -9px;
		}
		&:after {
			top: 6px;
		}
	}
	&-x {
		.bars {
			background-color: transparent !important;
			visibility: hidden;
			&:after {
				visibility: visible;
				opacity: 1;
				transform: rotateZ(45deg);
				top: -1.5px;
			}
			&:before {
				visibility: visible;
				transform: rotateZ(-45deg);
				top: 1.5px;
			}
		}
	}
}

body.open-rmenu {
	overflow:hidden !important;
	.rmenu {
		top: 0;
	}
	#logo-real * {
		fill: $pink;
	}
}


.header-scroll {
	.rmenu__ul {
		padding: 70px 0 0 0;
	}
}